  background: -webkit-linear-gradient(77deg, #d0c0fa, #f5ddff);
  background: -o-linear-gradient(77deg, #d0c0fa, #f5ddff);
  background: linear-gradient(13deg, #d0c0fa, #f5ddff);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
