.grid__container {
  height: 500px;
  width: 500px;
  background: -webkit-linear-gradient(225deg, #fff, #e8e9eb 600%);
  background: -o-linear-gradient(225deg, #fff, #e8e9eb 600%);
  background: linear-gradient(-135deg, #fff, #e8e9eb 600%);
  background: -webkit-repeating-linear-gradient(45deg, #fafafa, #fafafa 10px, #eee 10px, #eee 12px);
  background: -o-repeating-linear-gradient(45deg, #fafafa, #fafafa 10px, #eee 10px, #eee 12px);
  background: repeating-linear-gradient(45deg, #fafafa, #fafafa 10px, #eee 10px, #eee 12px);
