}

#grid {
  height: 100%;
  width: 100%;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(4, 25%);
  grid-auto-rows: 25%;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.game__wrapper {
  width: 500px;
}

.card {
  font-size: 1.618em;
  position: relative;
  height: 100%;
  width: 100%;
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.card__side {
  border: 2px solid black;
  -webkit-box-shadow: 0 3px 10px -3px rgba(0, 0, 0, 0.269);
          box-shadow: 0 3px 10px -3px rgba(0, 0, 0, 0.269);
  border-radius: 15px;
  position: absolute;
  height: 100%;
  width: 100%;
  /* Just with this line below, this now works in safari */
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.card__front {
  background: #fafafa;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card__back {
  background: -webkit-linear-gradient(315deg, #b5e8e8, #59dcda 200%);
  background: -o-linear-gradient(315deg, #b5e8e8, #59dcda 200%);
  background: linear-gradient(135deg, #b5e8e8, #59dcda 200%);
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

/* ANIMATIONS */

.flipped {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.wiggle {
  -webkit-animation: 0.5s wiggle linear;
          animation: 0.5s wiggle linear;
}

.bounce {
  -webkit-animation: 0.5s bounce linear;
          animation: 0.5s bounce linear;
}

/* this animation was resetting the card's transform property */
/* to maintain the property I needed to set rotateY(180deg) on evert frame */
@-webkit-keyframes wiggle {
  0% {
      -webkit-transform: rotate(0deg) rotateY(180deg);
              transform: rotate(0deg) rotateY(180deg);
  }
  25% {
      -webkit-transform: rotate(-10deg) rotateY(180deg);
              transform: rotate(-10deg) rotateY(180deg);
  }
  50% {
      -webkit-transform: rotate(20deg) rotateY(180deg);
              transform: rotate(20deg) rotateY(180deg);
  }
  75% {
      -webkit-transform: rotate(-5deg) rotateY(180deg);
              transform: rotate(-5deg) rotateY(180deg);
  }
  100% {
      -webkit-transform: rotate(0deg) rotateY(180deg);
              transform: rotate(0deg) rotateY(180deg);
  }
}
@keyframes wiggle {
  0% {
      -webkit-transform: rotate(0deg) rotateY(180deg);
              transform: rotate(0deg) rotateY(180deg);
  }
  25% {
      -webkit-transform: rotate(-10deg) rotateY(180deg);
              transform: rotate(-10deg) rotateY(180deg);
  }
  50% {
      -webkit-transform: rotate(20deg) rotateY(180deg);
              transform: rotate(20deg) rotateY(180deg);
  }
  75% {
      -webkit-transform: rotate(-5deg) rotateY(180deg);
              transform: rotate(-5deg) rotateY(180deg);
  }
  100% {
      -webkit-transform: rotate(0deg) rotateY(180deg);
              transform: rotate(0deg) rotateY(180deg);
  }
}

@-webkit-keyframes bounce {
  0% {
      -webkit-transform: translateY(0) rotateY(180deg);
              transform: translateY(0) rotateY(180deg);
  }
  25% {
      -webkit-transform: translateY(-10px) rotateY(180deg);
              transform: translateY(-10px) rotateY(180deg);
  }
  50% {
      -webkit-transform: translateY(3px) rotateY(180deg);
              transform: translateY(3px) rotateY(180deg);
  }
  75% {
      -webkit-transform: translateY(-3px) rotateY(180deg);
              transform: translateY(-3px) rotateY(180deg);
  }
  100% {
      -webkit-transform: translateY(0) rotateY(180deg);
              transform: translateY(0) rotateY(180deg);
  }
}

@keyframes bounce {
  0% {
      -webkit-transform: translateY(0) rotateY(180deg);
              transform: translateY(0) rotateY(180deg);
  }
  25% {
      -webkit-transform: translateY(-10px) rotateY(180deg);
              transform: translateY(-10px) rotateY(180deg);
  }
  50% {
      -webkit-transform: translateY(3px) rotateY(180deg);
              transform: translateY(3px) rotateY(180deg);
  }
  75% {
      -webkit-transform: translateY(-3px) rotateY(180deg);
              transform: translateY(-3px) rotateY(180deg);
  }
  100% {
      -webkit-transform: translateY(0) rotateY(180deg);
              transform: translateY(0) rotateY(180deg);
  }
}

